import React from 'react';
import {Outlet, Route, Routes, RoutesProps} from 'react-router-dom';

const ErrorBoundaryRoutes = ({ children }: RoutesProps) => {
  return (
    <Routes>
      <Route
        element={
          <Outlet />}
      >
        {children}
      </Route>
    </Routes>
  );
};

export default ErrorBoundaryRoutes;
